.title {
  color: yellow;
  text-align: center;
}
.container {
  display: grid;
  grid-template-columns: 70% 30%;
  padding-top: 3rem;
}
.container__list {
  display: grid;
  grid-template-columns: 33% 33% 33%;
}

.container__pokedex {
  position: relative;
  display: block;
}

.card {
  padding: 1.5rem;
  background: -webkit-linear-gradient(to bottom, #96c93d, #00b09b);
  background: linear-gradient(to bottom, #96c93d, #00b09b);
  text-align: center;
  align-content: center;
  margin-right: 1rem;
  margin-left: 1rem;
  margin-bottom: 2rem;
  width: 200px;
  height: 150px;
  border-radius: 1rem;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
}
.poke-text {
  text-align: center;
  font-weight: 700;
}

.img {
  height: 920px;
  width: 400px;
  position: relative;
}
.pokedex {
  text-align: center;
  position: absolute;
  top: 20%;
  left: 6%;
  width: 350px;
}
.poke_title {
  margin: auto;
}
.pokemon_title {
  margin-top: 4px;
  margin-bottom: 0;
  font-weight: bolder;
}

.pokemon__img {
  height: 147px;
}

.pokemon__img.anim {
  animation-name: left-to-right;
  animation-duration: 3s;
}

@keyframes left-to-right {
  0% {
    transform: translateX(80%);
  }
  25% {
    transform: translateY(-35%);
  }
  50% {
    transform: translateX(-80%);
  }
  75% {
    transform: translateX(60%);
  }
  90% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0%);
  }
}

.info__type {
  font-weight: bold;
  align-content: center;
  display: inline-block;
  width: 140px;
  height: 35px;
  background: -webkit-linear-gradient(to bottom, #96c93d, #00b09b);
  background: linear-gradient(to bottom, #96c93d, #00b09b);
  padding-bottom: 15px;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 3px;
  border-radius: 8px;
}

.info__abilities {
  font-weight: bold;
  display: inline-block;
  width: 140px;
  height: 40px;
  background: #ff512f;
  background: -webkit-linear-gradient(to right, #f09819, #ff512f);
  background: linear-gradient(to right, #f09819, #ff512f);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  padding-bottom: 1rem;
  border-radius: 8px;
  margin-top: 5px;
}

.stats_s {
  margin-top: 15px;
}
.poke_stats {
  margin-top: 5%;
}
.stats_info {
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: 1px;
}
.stats_info div {
  margin-top: 5px;
  font-size: medium;
  font-weight: bold;
  background-image: linear-gradient(
    to right,
    #c1c161 0%,
    #c1c161 0%,
    #d4d4b1 50%
  );
  margin-left: 2%;
  margin-right: 2%;
  border-radius: 8px;
}
.container__pokedex__info {
  text-align: center;
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
  height: 630px;
  width: 400px;
  margin-top: 2rem;
  margin-left: 35%;
  border-radius: 0.5rem;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
}
.close_button {
  padding: 5px;
  margin-left: 80%;
  margin-top: 5%;
}
.container__pokedex__info.close {
  display: none;
}
.search {
  margin-left: 33%;
  margin-bottom: 2rem;
  width: 400px;
  height: 30px;
  border-radius: 3px;
  border: none;
  outline: 0;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
}
.search :focus {
  outline: none;
}

button {
  border: none;
  border-radius: 3px;
  background: #363636;
  color: white;
  padding: 10px;
}

button:hover {
  background-color: #221e1e;
}

button:active {
  background: black;
}

.button_search {
  height: 34px;
  width: 70px;
}

.page {
  color: #f9d423;
  margin-left: 2rem;
}
.button-page {
  margin-left: 1rem;
}

@media only screen and (max-width: 992px) {
  .title {
    text-align: center;
  }
  .container {
    display: block;
  }
  .container__list {
    display: grid;
    grid-template-columns: 50% 50%;
    padding-top: 3rem;
  }
  .search {
    margin-left: 12.5%;
    margin-bottom: 2rem;
    width: 60%;
    height: 30px;
  }
  .search_content {
    display: flex;
    width: 100%;
  }
  .card {
    width: auto;
    height: 90px;
    margin-top: 10px;
    margin-bottom: 4px;
  }
  .pokedex_img {
    width: 70%;
    height: 100%;
    position: fixed;
    top: 1.5%;
    left: 17%;
  }
  .img {
    width: 100%;
    height: 98%;
    position: relative;
  }
  .poke-text {
    margin-top: 0;
  }
  .poke-img {
    height: 60px;
    width: 85px;
  }
  .container__pokedex {
    top: 0;
    left: 0;
    width: 100%;
    position: fixed;
    padding-left: 1px;
    display: none;
    z-index: 999;
    min-height: 100vh;
    background-color: rgba(0, 0, 0, 0.75);
  }
  .container__pokedex.option {
    display: flex;
  }

  .pokedex {
    align-content: center;
    width: 64%;
    position: fixed;
    top: 20%;
    left: 20%;
  }
  .pokemon_title {
    margin-top: 3px;
    font-size: 95%;
  }
  .pokemon__img {
    align-content: center;
    height: 80px;
    width: 80px;
  }

  .info__type {
    font-size: 60%;
    align-content: center;
    display: inline-block;
    width: 75px;
    height: 20px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 1px;
    padding-bottom: 10px;
  }

  .info__abilities {
    font-size: 60%;
    width: 75px;
    height: 20px;
    margin-left: 5px;
    margin-right: 5px;
    padding-bottom: 10px;
  }

  .container__pokedex__info {
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-left: 15%;
    height: 450px;
    width: 70%;
  }

  .poke_stats {
    margin-top: 1%;
  }
  .stats_info div {
    font-size: 62%;
  }
}
@media only screen and (min-width: 768px) and (max-width: 992px) {
  .info__type {
    width: 120px;
    height: 30px;
  }

  .info__abilities {
    width: 120px;
    height: 30px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .container {
    display: grid;
    grid-template-columns: 60% 35%;
  }

  .card {
    width: auto;
    height: auto;
  }
}
